"use client";

import { useState, useEffect } from "react";
import {
  Box,
  Typography,
  Paper,
  Divider,
  Button,
  Chip,
  Stack,
  useTheme,
  CircularProgress,
  Tabs,
  Tab,
  Card,
  CardContent,
  IconButton,
  Tooltip,
  Snackbar,
  Alert,
} from "@mui/material";
import {
  CheckCircle as CheckCircleIcon,
  Bookmark as BookmarkIcon,
  Share as ShareIcon,
  Code as CodeIcon,
  Lightbulb as LightbulbIcon,
  Timer as TimerIcon,
  Check as CheckIcon,
  Close as CloseIcon,
} from "@mui/icons-material";
import { Prism as SyntaxHighlighter } from "react-syntax-highlighter";
import { atomDark } from "react-syntax-highlighter/dist/cjs/styles/prism";
import { dsaService } from "@/services/dsaService";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`problem-tabpanel-${index}`}
      aria-labelledby={`problem-tab-${index}`}
      {...other}
    >
      {value === index && <Box sx={{ p: 3 }}>{children}</Box>}
    </div>
  );
}

const ChapterDetail = ({ chapter, subtopics = [], onUpdate }) => {
  const theme = useTheme();
  const [activeTab, setActiveTab] = useState(0);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [completedSubtopicIds, setCompletedSubtopicIds] = useState([]);
  const [snackbar, setSnackbar] = useState({
    open: false,
    message: "",
    severity: "success",
  });

  const currentSubtopic = subtopics[activeTab];
  const isCompleted =
    currentSubtopic && completedSubtopicIds.includes(currentSubtopic._id);

  useEffect(() => {
    const fetchCompletedSubtopics = async () => {
      try {
        const response = await api.get("/subtopics/completed");
        setCompletedSubtopicIds(response.data?.map((item) => item._id) || []);
      } catch (err) {
        console.error("Error fetching completed subtopics:", err);
        setError("Failed to load completion status");
      }
    };

    if (subtopics.length > 0) {
      fetchCompletedSubtopics();
    }
  }, [subtopics]);

  const handleToggleComplete = async () => {
    if (!currentSubtopic) return;

    try {
      setLoading(true);
      setError(null);

      if (isCompleted) {
        await api.delete(`/subtopics/${currentSubtopic._id}/complete`);
        setCompletedSubtopicIds((prev) =>
          prev.filter((id) => id !== currentSubtopic._id)
        );
        setSnackbar({
          open: true,
          message: "Marked as not completed",
          severity: "info",
        });
      } else {
        await api.post(`/subtopics/${currentSubtopic._id}/complete`);
        setCompletedSubtopicIds((prev) => [...prev, currentSubtopic._id]);
        setSnackbar({
          open: true,
          message: "Marked as completed!",
          severity: "success",
        });
      }

      if (onUpdate) {
        onUpdate();
      }
    } catch (err) {
      console.error("Error updating completion status:", err);
      setError("Failed to update status");
      setSnackbar({
        open: true,
        message: "Failed to update status",
        severity: "error",
      });
    } finally {
      setLoading(false);
    }
  };

  const handleCloseSnackbar = () => {
    setSnackbar((prev) => ({ ...prev, open: false }));
  };

  if (!chapter) {
    return (
      <Paper
        elevation={0}
        sx={{
          p: 4,
          height: "100%",
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          justifyContent: "center",
          textAlign: "center",
          minHeight: 400,
          borderRadius: 2,
          backgroundColor: theme.palette.background.default,
          border: `1px solid ${theme.palette.divider}`,
        }}
      >
        <Typography variant="h6" color="text.secondary">
          Select a chapter to view details
        </Typography>
      </Paper>
    );
  }

  if (subtopics.length === 0) {
    return (
      <Paper
        elevation={0}
        sx={{
          p: 4,
          height: "100%",
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          justifyContent: "center",
          textAlign: "center",
          minHeight: 400,
          borderRadius: 2,
          backgroundColor: theme.palette.background.default,
          border: `1px solid ${theme.palette.divider}`,
        }}
      >
        <Typography variant="h6" color="text.secondary">
          No subtopics available for this chapter
        </Typography>
      </Paper>
    );
  }

  return (
    <Paper
      elevation={0}
      sx={{
        p: { xs: 2, md: 3 },
        height: "100%",
        borderRadius: 2,
        backgroundColor: theme.palette.background.default,
        border: `1px solid ${theme.palette.divider}`,
        display: "flex",
        flexDirection: "column",
      }}
    >
      <Box sx={{ mb: 3 }}>
        <Box
          sx={{
            display: "flex",
            justifyContent: "space-between",
            alignItems: "flex-start",
          }}
        >
          <Box>
            <Typography variant="h5" component="h2" gutterBottom>
              {chapter.name}
            </Typography>
            <Typography variant="body1" color="text.secondary" paragraph>
              {chapter.description}
            </Typography>
          </Box>

          <Button
            variant={isCompleted ? "outlined" : "contained"}
            color={isCompleted ? "secondary" : "primary"}
            onClick={handleToggleComplete}
            disabled={loading}
            startIcon={isCompleted ? <CloseIcon /> : <CheckIcon />}
            sx={{ minWidth: 180 }}
          >
            {loading ? (
              <CircularProgress size={24} />
            ) : isCompleted ? (
              "Mark as Incomplete"
            ) : (
              "Mark as Completed"
            )}
          </Button>
        </Box>

        <Box sx={{ display: "flex", gap: 1, mb: 2 }}>
          <Chip
            label={chapter.difficulty}
            size="small"
            color={getLevelColor(chapter.difficulty)}
            variant="outlined"
          />
        </Box>

        <Divider sx={{ my: 2 }} />

        <Tabs
          value={activeTab}
          onChange={(e, newValue) => setActiveTab(newValue)}
          variant="scrollable"
          scrollButtons="auto"
          sx={{ mb: 2 }}
        >
          {subtopics.map((subtopic) => (
            <Tab
              key={subtopic._id}
              value={subtopic._id}
              label={subtopic.name}
              icon={
                subtopic.isCompleted ? <CheckCircle fontSize="small" /> : null
              }
              iconPosition="end"
            />
          ))}
        </Tabs>

        {currentSubtopic && (
          <Box sx={{ mt: 2 }}>
            <Box
              sx={{
                display: "flex",
                justifyContent: "space-between",
                alignItems: "center",
                mb: 2,
              }}
            >
              <Typography variant="h6">{currentSubtopic.name}</Typography>
              <Chip
                label={currentSubtopic.difficulty}
                size="small"
                color={getLevelColor(currentSubtopic.difficulty)}
              />
            </Box>

            <Box sx={{ mb: 3 }}>
              <Button
                variant={currentSubtopic.isCompleted ? "outlined" : "contained"}
                color="primary"
                startIcon={currentSubtopic.isCompleted ? <CheckCircle /> : null}
                onClick={() =>
                  onToggleComplete(chapter._id, currentSubtopic._id)
                }
                sx={{ mr: 2, mb: { xs: 2, sm: 0 } }}
              >
                {currentSubtopic.isCompleted
                  ? "Completed"
                  : "Mark as Completed"}
              </Button>
            </Box>

            {currentSubtopic.youtubeLink && (
              <Box sx={{ mb: 3 }}>
                <Typography variant="subtitle2" gutterBottom>
                  Video Tutorial
                </Typography>
                <Button
                  variant="outlined"
                  href={currentSubtopic.youtubeLink}
                  target="_blank"
                  rel="noopener noreferrer"
                  startIcon={<YouTube />}
                  sx={{ mr: 2, mb: 1 }}
                >
                  Watch on YouTube
                </Button>
              </Box>
            )}

            {currentSubtopic.leetcodeLink && (
              <Box sx={{ mb: 3 }}>
                <Typography variant="subtitle2" gutterBottom>
                  Practice Problems
                </Typography>
                <Button
                  variant="outlined"
                  href={currentSubtopic.leetcodeLink}
                  target="_blank"
                  rel="noopener noreferrer"
                  startIcon={<Code />}
                  sx={{ mr: 2, mb: 1 }}
                >
                  LeetCode Problems
                </Button>
              </Box>
            )}

            {currentSubtopic.articleLink && (
              <Box sx={{ mb: 3 }}>
                <Typography variant="subtitle2" gutterBottom>
                  Read More
                </Typography>
                <Button
                  variant="outlined"
                  href={currentSubtopic.articleLink}
                  target="_blank"
                  rel="noopener noreferrer"
                  startIcon={<Article />}
                >
                  Read Article
                </Button>
              </Box>
            )}
          </Box>
        )}
      </Box>
    </Paper>
  );
};

export default ChapterDetail;
